import 'src/assets/font/iconfont.css';

export default {
  computed: {
    visible() {
      return ['/', '/header', '/search'].indexOf(this.$route.path) < 0;
    }
  },
  mounted() {
    if (window.location.hash.indexOf('smile-select') > -1) {
      history.back();
    }
  }
};



