import axios from 'axios';
export default {
  name: 'page-field',
  data() {
    return {
      fileList: []
    };
  },
  methods: {
    getFileList() {
      axios.get('/mock/fileList.json').then(resp => {
        let respData = resp.data;
        if (respData.success === true) {
          this.fileList = respData.items;
        }
      });
    }
  },
  mounted() {
    this.getFileList();
  }
};



