/**
 * mt-tab-item
 * @module components/tab-item
 * @desc 搭配 tabbar 或 navbar 使用
 * @param {*} id - 选中后的返回值，任意类型
 * @param {slot} [icon] - icon 图标
 * @param {slot} - 文字
 *
 * @example
 * <mt-tab-item>
 *   <img slot="icon" src="http://placehold.it/100x100">
 *   订单
 * </mt-tab-item>
 */

/**
 * @noteType component
 * @name TabItem
 * @tagName mt-tab-item
 * @desc 选项卡头节点
 * @type tabItem
 * @wrapClassName mint-tab-item
 * @uniqueKey id
 * @noDisplayInComponents true
 * @html
 * <mt-tab-item id="1">{{space}}</mt-tab-item>
 */
export default {
  name: 'mt-tab-item',

    /**
     * @noteType prop
     * @field id
     * @desc id
     * @type input
     * @value
     */
  props: ['id']
};

/**
 * @noteType external
 * @content
 {
   "mock": {
      "space": "tab"
    },
    "textField": {
      "field": "space",
      "className": "mint-tab-item-label"
    }
 }
 */



