import XCell from 'bh-mint-ui2/packages/cell/index.js';
if (process.env.NODE_ENV === 'component') {
  require('bh-mint-ui2/packages/cell/style.css');
}
/**
 * mt-radio
 * @module components/radio
 * @desc 单选框列表，依赖 cell 组件
 *
 * @param {string[], object[]} options - 选项数组，可以传入 [{label: 'label', value: 'value', disabled: true}] 或者 ['ab', 'cd', 'ef']
 * @param {string} value - 选中值
 * @param {string} title - 标题
 * @param {string} [align=left] - checkbox 对齐位置，`left`, `right`
 *
 * @example
 * <mt-radio v-model="value"></mt-radio>
 */

/**
 * @noteType component
 * @name Radio
 * @tagName mt-radio
 * @desc 单选列表
 * @wrapClassName mint-radio
 * @html
 * <mt-radio v-model="value" title="" :options="['a', 'b', 'c']"></mt-radio>
 */
export default {
  name: 'mt-radio',

  props: {
    /**
     * @noteType prop
     * @field title
     * @desc 标题
     * @type input
     */
    title: String,
    /**
     * @noteType prop
     * @field align
     * @desc 对其位置
     * @type select
     * @option
     [{
        "text": "left",
         "value": "left"
      },{
        "text": "right",
         "value": "right"
      }]
      */
    align: String,
    /**
     * @noteType prop
     * @field options
     * @desc 选择项
     * @type textarea
     * @valueType object
      */
    options: {
      type: Array,
      required: true
    },
    /**
     * @noteType prop
     * @field value
     * @desc 绑定值
     * @type textarea
     * @valueType object
      */
    value: String,
    //radio类型,hook类型
    type:{
      type:String,
      default:''
    },
    inline:Boolean
  },

  data() {
    return {
      currentValue: this.value
    };
  },

  watch: {
    value(val) {
      this.currentValue = val;
    },

    currentValue(val) {
      this.$emit('input', val);
    }
  },

  components: {
    XCell
  }
};
/**
 * @noteType external
 * @content
 {
   "mock": {
      "title": "单选列表",
      "options": [{"label":"选项A","value":"A"}, {"label":"选项B","value":"B"}],
      "value": []
   },
   "xtype": [ "radiolist" ],
   "modelName": "value",
   "bindField": "v-model"
 }
 */



