import XCell from 'bh-mint-ui2/packages/cell/index.js';
if (process.env.NODE_ENV === 'component') {
  require('bh-mint-ui2/packages/cell/style.css');
}

/**
 * mt-checklist
 * @module components/checklist
 * @desc 复选框列表，依赖 cell 组件
 *
 * @param {(string[]|object[])} options - 选项数组，可以传入 [{label: 'label', value: 'value', disabled: true}] 或者 ['ab', 'cd', 'ef']
 * @param {string[]} value - 选中值的数组
 * @param {string} title - 标题
 * @param {number} [max] - 最多可选的个数
 * @param {string} [align=left] - checkbox 对齐位置，`left`, `right`
 *
 *
 * @example
 * <mt-checklist :v-model="value" :options="['a', 'b', 'c']"></mt-checklist>
 */

/**
 * @noteType component
 * @name CheckList
 * @tagName mt-checklist
 * @desc 多选列表
 * @wrapClassName mint-checklist
 * @html
 * <mt-checklist v-model="value"></mt-checklist>
 */
export default {
  name: 'mt-checklist',

  props: {
    /**
     * @noteType prop
     * @field max
     * @desc 最多可选个数
     * @type input
     * @valueType number
     */
    max: Number,
    /**
     * @noteType prop
     * @field title
     * @desc 标题
     * @type input
     */
    title: String,
    /**
     * @noteType prop
     * @field align
     * @desc 对其位置
     * @type select
     * @option
     [{
        "text": "left",
         "value": "left"
      },{
        "text": "right",
         "value": "right"
      }]
      */
    align: String,
    /**
     * @noteType prop
     * @field options
     * @desc 选择项
     * @type textarea
     * @valueType object
      */
    options: {
      type: Array,
      required: true
    },
    /**
     * @noteType prop
     * @field value
     * @desc 默认值
     * @type textarea
     * @valueType object
      */
    value: Array
  },

  components: { XCell },

  data() {
    return {
      currentValue: this.value
    };
  },

  computed: {
    limit() {
      return this.max < this.currentValue.length;
    }
  },

  watch: {
    value(val) {
      this.currentValue = val;
    },

    currentValue(val) {
      if (this.limit) val.pop();
      this.$emit('input', val);
    }
  }
};

/**
 * @noteType external
 * @content
 {
   "mock": {
      "title": "复选框列表",
      "max": 100,
      "options": [{"label":"选项A","value":"A"}, {"label":"选项B","value":"B"}],
      "value": []
   },
   "xtype": [ "checkboxlist" ],
   "modelName": "value",
   "bindField": "v-model"
 }
 */



